/* eslint-disable react/prop-types */
import React from "react";
import ReactDOM from "react-dom";
import ReactModal from "react-modal";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
// import InputAdornment from "@material-ui/core/InputAdornment";
import MomentUtils from "@date-io/moment";
import moment from "moment";
import { MuiPickersUtilsProvider, DatePicker } from "material-ui-pickers";
import GoogleMapReact from "google-map-react";
import axios from "axios";
// @material-ui/icons
import {
  InputAdornment,
  Tooltip,
  FormControlLabel,
  Checkbox,
  MenuItem,
  OutlinedInput,
  InputLabel,
  Select,
  FormControl
} from "@material-ui/core";
import { MyLocation, LocationOn, Check } from "@material-ui/icons";
// core components
import Header from "components/Header/Header.jsx";
import Footer from "components/Footer/Footer.jsx";
import Card from "components/Card/Card.jsx";
import CardBody from "components/Card/CardBody.jsx";
import CardFooter from "components/Card/CardFooter.jsx";
import CardHeader from "components/Card/CardHeader.jsx";
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import Button from "components/CustomButtons/Button.jsx";
import CustomInput from "components/CustomInput/CustomInput.jsx";
import HeaderLinks from "components/Header/HeaderLinks.jsx";
import Parallax from "components/Parallax/Parallax.jsx";

import landingPageStyle from "assets/jss/material-kit-react/views/landingPage.jsx";

// Sections for this page
import ProductSection from "./Sections/ProductSection.jsx";
import TeamSection from "./Sections/TeamSection.jsx";
import WorkSection from "./Sections/WorkSection.jsx";
import Summary from "./Sections/Summary.jsx";
import LocationAutosuggest from "../Components/Sections/LocationAutosuggest";
import loadingImg from "../../assets/img/loading.gif";
const dashboardRoutes = [];
ReactModal.defaultStyles.overlay.backgroundColor = "gray";
const { key } = require("../../vars");
class LandingPage extends React.Component {
  state = {
    center: [60.938043, 30.337157],
    zoom: 9,
    draggable: true,
    lat: 60.955413,
    lng: 30.337844,
    startDate: moment(),
    endDate: undefined,
    startDateString: "",
    endDateString: "",
    startLocation: "",
    endLocation: "",
    passengers: 0,
    bags: 0,
    selectLocation: false,
    selectDates: false,
    travellingBothWay: false,
    selectPassengers: false,
    withBags: false,
    action: undefined,
    error: false,
    loading: false,
    showCars: false,
    selectCar: null,
    showMap: false
  };
  startLocation = location => {
    this.setState({
      startLocation: location,
      selectLocation: false,
      action: "LOCATION",
      error: false
    });
  };
  endLocation = location => {
    this.setState({ endLocation: location, action: "LOCATION", error: false });
  };
  startDate = date => {
    this.setState({
      startDate: date,
      startDateString: date.format("D-M-Y"),
      selectDates: false,
      action: "DATE",
      error: false
    });
  };
  endDate = date => {
    this.setState({
      endDate: date,
      endDateString: date.format("D-M-Y"),
      action: "DATE",
      error: false
    });
  };
  passengers = number => {
    this.setState({ passengers: number, error: false });
  };
  selectCar = type => {
    this.setState({ selectCar: type, showCars: false });
  };
  bags = number => {
    this.setState({
      bags: number,
      withBags: number ? true : false,
      error: false
    });
  };
  getQuotes = action => {
    let date = moment();
    const {
      startLocation,
      endLocation,
      startDateString,
      endDateString,
      passengers,
      travellingBothWay,
      withBags,
      bags
    } = this.state;
    switch (action) {
      case "LOCATION":
        if (startLocation && endLocation) {
          this.setState({
            selectLocation: true,
            action: "DATE",
            error: false,
            startDateString: date.format("D-M-Y")
          });
        } else {
          this.setState({ error: true });
        }
        break;
      case "DATE":
        if (startDateString && (!travellingBothWay || endDateString)) {
          this.setState({
            selectDates: true,
            action: "PASSENGER",
            error: false
          });
        } else {
          this.setState({ error: true });
        }
        break;
      case "PASSENGER":
        if (passengers && (!withBags || bags)) {
          this.setState({
            loading: true,
            selectPassengers: false,
            action: "SEARCH",
            error: false
          });
          setTimeout(() => {
            this.setState({ showCars: true });
          }, 3000);
        } else {
          this.setState({ error: true });
        }
        break;
      default:
        this.setState({ error: true });
        break;
    }
  };
  travellingBothWay = () => {
    let returnJourney = !this.state.travellingBothWay;
    let endDate = moment(this.state.startDate, "DD-MM-YYYY").add(1, "days");
    this.setState({
      error: false,
      travellingBothWay: returnJourney,
      endDate: returnJourney ? endDate : null,
      endDateString: returnJourney ? endDate.format("D-M-Y") : "",
      action: "DATE"
    });
  };
  queryFormHeader = () => {
    return (
      <CardHeader
        style={{
          paddingTop: "40px",
          borderBottom: "1px solid blue"
        }}
      >
        {!this.state.selectLocation ? (
          <h4>
            <b>
              Book your TRIP with <i>ETN</i>
            </b>
          </h4>
        ) : (
          <h4
            className="mt-10"
            onClick={() => {
              this.startLocation(this.state.startLocation);
            }}
          >
            <b className="highlight">
              From:
              {this.state.startLocation}
            </b>{" "}
            <br />
            <b className="highlight">To: {this.state.endLocation}</b>
          </h4>
        )}
        {this.state.selectDates && (
          <h4
            className="mt-10"
            onClick={() => {
              this.startDate(this.state.startDate);
            }}
          >
            <b className="highlight">Date: {this.state.startDateString}</b>
            <br />
            {this.state.endDateString && (
              <b>
                Returns:
                <i>{this.state.endDateString}</i>
              </b>
            )}
          </h4>
        )}
      </CardHeader>
    );
  };
  selectCurrent = async () => {
    let options = {
      enableHighAccuracy: true,
      timeout: 5000,
      maximumAge: 0
    };
    await window.navigator.geolocation.getCurrentPosition(
      async ({ coords }) => {
        let { latitude, longitude } = coords;
        let address = await this.getAddressFromCoords(latitude, longitude);
        this.setState({
          startLocation: latitude + ", " + longitude,
          lat: latitude,
          lng: longitude,
          center: [latitude, longitude]
        });
      },
      e => {},
      options
    );
  };
  showMap = async () => {
    this.setState({ showMap: true });
  };
  hideMap = () => {
    this.setState({ showMap: false });
  };
  queryFormBody = classes => {
    return (
      <CardBody>
        {!this.state.selectLocation && (
          <React.Fragment>
            <GridItem>
              <LocationAutosuggest
                label="Pick-up location"
                value={this.state.startLocation}
                onChange={this.startLocation}
                adornment={{
                  position: "end",
                  icon: <MyLocation />,
                  tooltip: "Pick my current location",
                  onClick: this.selectCurrent
                }}
              />
            </GridItem>
            <GridItem>
              <LocationAutosuggest
                label="Drop off location"
                value={this.state.endLocation}
                onChange={this.endLocation}
                adornment={{
                  position: "end",
                  icon: <LocationOn />,
                  tooltip: "Locate on Map",
                  onClick: this.showMap
                }}
              />
            </GridItem>
          </React.Fragment>
        )}
        {this.state.selectLocation && !this.state.selectDates && (
          <React.Fragment>
            <GridItem>
              <MuiPickersUtilsProvider
                utils={MomentUtils}
                style={{ width: "100%" }}
              >
                <DatePicker
                  margin="normal"
                  label={`Journey Date`}
                  value={this.state.startDate || new Date()}
                  onChange={e => {
                    this.startDate(e);
                  }}
                />
              </MuiPickersUtilsProvider>
            </GridItem>
            {this.state.travellingBothWay && (
              <GridItem>
                <MuiPickersUtilsProvider
                  utils={MomentUtils}
                  style={{ width: "100%" }}
                >
                  <DatePicker
                    margin="normal"
                    label={`Return Date`}
                    value={this.state.endDate || new Date()}
                    onChange={e => {
                      this.endDate(e);
                    }}
                  />
                </MuiPickersUtilsProvider>
              </GridItem>
            )}
            <GridItem>
              <div
                className={
                  classes.checkboxAndRadio +
                  " " +
                  classes.checkboxAndRadioHorizontal
                }
              >
                <FormControlLabel
                  control={
                    <Checkbox
                      tabIndex={-1}
                      onClick={() => this.travellingBothWay()}
                      checked={this.state.travellingBothWay}
                      checkedIcon={<Check className={classes.checkedIcon} />}
                      icon={<Check className={classes.uncheckedIcon} />}
                      classes={{ checked: classes.checked }}
                    />
                  }
                  classes={{ label: classes.label }}
                  label="Return?"
                />
              </div>
            </GridItem>
          </React.Fragment>
        )}
        {this.state.selectLocation &&
          this.state.selectDates &&
          !this.state.selectPassengers && (
            <React.Fragment>
              <GridItem>
                <FormControl variant="outlined" className={classes.formControl}>
                  <InputLabel
                    style={{ backgroundColor: "white" }}
                    ref={ref => {
                      this.InputLabelRef = ref;
                    }}
                    htmlFor="passengers"
                  >
                    Passengers
                  </InputLabel>
                  <Select
                    style={{
                      width: "16rem"
                    }}
                    value={this.state.passengers}
                    onChange={e => this.passengers(e.target.value)}
                    input={
                      <OutlinedInput
                        labelWidth={1}
                        name="age"
                        id="passengers"
                      />
                    }
                  >
                    <MenuItem value={1}>1</MenuItem>
                    <MenuItem value={2}>2</MenuItem>
                    <MenuItem value={3}>3</MenuItem>
                    <MenuItem value={4}>4</MenuItem>
                    <MenuItem value={5}>5</MenuItem>
                    <MenuItem value={6}>6</MenuItem>
                    <MenuItem value={7}>7</MenuItem>
                    <MenuItem value={8}>8</MenuItem>
                    <MenuItem value={9}>9</MenuItem>
                    <MenuItem value={10}>10</MenuItem>
                    <MenuItem value={11}>11</MenuItem>
                    <MenuItem value={12}>12</MenuItem>
                  </Select>
                </FormControl>
              </GridItem>
              <br />
              <GridItem>
                <FormControl variant="outlined" className={classes.formControl}>
                  <InputLabel
                    style={{ backgroundColor: "white" }}
                    ref={ref => {
                      this.InputLabelRef = ref;
                    }}
                    htmlFor="luggage"
                  >
                    Luggage
                  </InputLabel>
                  <Select
                    style={{
                      width: "16rem"
                    }}
                    value={this.state.bags}
                    onChange={e => this.bags(e.target.value)}
                    input={
                      <OutlinedInput
                        labelWidth={1}
                        name="luggage"
                        id="luggage"
                      />
                    }
                  >
                    <MenuItem value="0">
                      <em>None</em>
                    </MenuItem>
                    <MenuItem value={1}>Luggage</MenuItem>
                    <MenuItem value={2}>Box</MenuItem>
                    <MenuItem value={3}>Household</MenuItem>
                    <MenuItem value={4}>Pet</MenuItem>
                    <MenuItem value={5}>Child Item</MenuItem>
                  </Select>
                </FormControl>
              </GridItem>
            </React.Fragment>
          )}
      </CardBody>
    );
  };
  loading = () => {
    return (
      <CardBody>
        <img alt="Loading ..." style={{ width: "100%" }} src={loadingImg} />
      </CardBody>
    );
  };
  onCircleInteraction = (childKey, childProps, mouse) => {
    this.setState({
      draggable: false,
      lat: mouse.lat,
      lng: mouse.lng,
      endLocation: mouse.lat + "," + mouse.lng
    });
  };
  onCircleInteraction3 = (childKey, childProps, mouse) => {
    this.setState({ draggable: true });
  };
  _onChange = ({ center, zoom }) => {
    this.setState({
      center: center,
      zoom: zoom
    });
  };
  getAddressFromCoords = (lat, long) => {
    return axios
      .get(
        `https://maps.googleapis.com/maps/api/geocode/json?key=${key}&latlng=${lat},${long}`
      )
      .then(response => {
        console.log(response.data.results);
      })
      .catch(e => {
        console.log(e.response);
      });
  };

  render() {
    const { classes, ...rest } = this.props;
    return (
      <div>
        {this.state.showMap ? (
          <ReactModal
            isOpen={this.state.showMap}
            contentLabel="Modal #1 Global Style Override Example"
            onRequestClose={this.hideMap}
            ariaHideApp={false}
          >
            <div style={{ height: "100vh", width: "100%" }}>
              <GoogleMapReact
                draggable={this.state.draggable}
                onChange={this._onChange}
                center={this.state.center}
                zoom={this.state.zoom}
                onChildMouseDown={this.onCircleInteraction}
                onChildMouseUp={this.onCircleInteraction3}
                onChildMouseMove={this.onCircleInteraction}
                onChildClick={() => console.log("child click")}
                onClick={() => console.log("mapClick")}
              >
                <div
                  style={{
                    color: "red",
                    fontSize: 50,
                    fontWeight: "bold"
                  }}
                  lat={this.state.lat}
                  lng={this.state.lng}
                >
                  ^
                </div>
              </GoogleMapReact>
            </div>
            <button onClick={this.hideMap}>Close Modal</button>
          </ReactModal>
        ) : (
          <div>
            <Header
              color="transparent"
              routes={dashboardRoutes}
              brand="Express Transfer Network"
              rightLinks={<HeaderLinks />}
              fixed
              changeColorOnScroll={{
                height: 60,
                color: "white"
              }}
              {...rest}
            />
            {this.state.showCars && (
              <div
                className={classNames(classes.main, classes.mainRaised)}
                style={{ marginTop: 100 }}
              >
                <div className={classes.container}>
                  <TeamSection selectMode={true} onSelect={this.selectCar} />
                </div>
              </div>
            )}
            {this.state.selectCar && (
              <div
                className={classNames(classes.main, classes.mainRaised)}
                style={{ marginTop: 100 }}
              >
                <div className={classes.container}>
                  <Summary state={this.state} />
                </div>
              </div>
            )}
            {!this.state.selectCar && !this.state.showCars && (
              <React.Fragment>
                <Parallax filter image={require("assets/img/landing-bg.png")}>
                  <div className={classes.container}>
                    <GridContainer style={{ paddingTop: 50 }}>
                      <GridItem xs={12} sm={12} md={4}>
                        <Card>
                          {!this.state.loading && this.queryFormHeader()}
                          {this.state.loading
                            ? this.loading()
                            : this.queryFormBody(classes)}
                          {!this.state.loading && (
                            <CardFooter>
                              <GridItem>
                                <Button
                                  fullWidth
                                  color={this.state.error ? "danger" : "info"}
                                  onClick={() => {
                                    this.getQuotes(this.state.action);
                                  }}
                                >
                                  Get Quote
                                </Button>
                              </GridItem>
                            </CardFooter>
                          )}
                        </Card>
                      </GridItem>
                    </GridContainer>
                  </div>
                </Parallax>
                <div className={classNames(classes.main, classes.mainRaised)}>
                  <div className={classes.container}>
                    <ProductSection />
                    <TeamSection />
                    <WorkSection />
                  </div>
                </div>
                <Footer />
              </React.Fragment>
            )}
          </div>
        )}
      </div>
    );
  }
}

export default withStyles(landingPageStyle)(LandingPage);
